.navBtn{
  display: block;
  height: 30px;
  width: 30px;
  position: fixed;
  top: 6px;
  left: -35px;
  background-size: 96% auto;
  background-position: 50% 50%;
  background-repeat: no-repeat;
   transition:all 600ms cubic-bezier(0.23, 1, 0.320, 1);
  background-image:url(../imgs/navs.png)
}

.navs {
  position: fixed;
  background-color: #fdfdfd;
  z-index: 999;
  height: 100%;
  width: 77%;
  max-width: 286px;
  top: 0;
  right: 0;
  box-shadow: 0 0 3px gainsboro;
  transition:all 700ms cubic-bezier(0.23, 1, 0.320, 1)
}
.hideIcon{
  opacity: 0;
}
.hideNav{
    transform: translateX(100%);
}
.links {
  padding-top: 1rem;
}
.navs a {
  display: block;
  line-height: 3rem;
  text-align: center;
  text-transform: capitalize;
  color: #666;
}
.navs a img {
  width: 1.2rem;
  vertical-align: middle;
}
.navs a strong {
  text-align: center;
  display: inline-block;
  width: 6rem;
}
.infosWrp {
  width: 100%;
  padding: 0;
  background-color: #fbf7e1;
}
.supports {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  text-transform: capitalize;
  font-size: 0.7rem;
  color: #ccc;
}